.searchContainer{
    background-image: rgb(224, 165, 165); 
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    z-index: 1;
    height: 80vh;
    width: 80vw;
    margin:auto;
    height: 10vh;
    opacity: 0.7;
    align-self: center;
    flex-direction: column;
    align-items: center;  
    text-align: center;
}

.Search{
    display: flex;
    align-self: center;
    color:var(--textColor);
    font-style:normal;
    font-weight:900;
    margin-left:auto;
    margin-right:auto;
}
.searchContainer img{
    height: 200px;
    width: 100px;
    margin-left:auto;
    margin-right:auto;
    margin-top:  100px;
    margin-bottom: 10px;
    padding:10px;
    align-self: center;
}




.searchbox{
    background-color:#fffbf8;
    padding:5px;
    width:300px;
    margin:0 auto;
    -webkit-box-sizing:border-box;
    -moz-box-sizing:border-box;
    box-sizing:border-box;
    border-radius:6px;
    -webkit-box-shadow:
        0 2px 4px 0 rgba(72, 72, 72, 0.83),
        0 10px 15px 0 rgba(126, 126, 126, 0.12),
        0 -2px 6px 1px rgba(199, 199, 199, 0.55) inset, 
        0 2px 4px 2px rgba(255, 255, 255, 0.83) inset;
    -moz-box-shadow: 
        0 2px 4px 0 rgba(72, 72, 72, 0.83),
        0 10px 15px 0 rgba(126, 126, 126, 0.12),
        0 -2px 6px 1px rgba(199, 199, 199, 0.55) inset, 
        0 2px 4px 2px rgba(255, 255, 255, 0.83) inset;
    box-shadow: 
        0 2px 4px 0 rgba(72, 72, 72, 0.83),
        0 10px 15px 0 rgba(126, 126, 126, 0.12),
        0 -2px 6px 1px rgba(199, 199, 199, 0.55) inset, 
        0 2px 4px 2px rgba(255, 255, 255, 0.83) inset;
}
input[type="search"]{
    width:600px;
    height:30px;
    padding-left:5px;
    border-radius:6px;
    border:none;
    color:#939393;
    font-weight:500;
    background-color:#fffbf8;
    -webkit-box-shadow:
        0 -2px 2px 0 rgba(199, 199, 199, 0.55),
        0 1px 1px 0 #fff,
        0 2px 2px 1px #fafafa,
        0 2px 4px 0 #b2b2b2 inset,
        0 -1px 1px 0 #f2f2f2 inset,
        0 15px 15px 0 rgba(233, 12, 12, 0.09) inset;
    -moz-box-shadow: 
        0 -2px 2px 0 rgba(199, 199, 199, 0.55),
        0 1px 1px 0 #fff,
        0 2px 2px 1px #fafafa,
        0 2px 4px 0 #b2b2b2 inset,
        0 -1px 1px 0 #f2f2f2 inset,
        0 15px 15px 0 rgba(41, 41, 41, 0.09) inset;
    box-shadow:
        0 -2px 2px 0 rgba(199, 199, 199, 0.55),
        0 1px 1px 0 #fff,
        0 2px 2px 1px #fafafa,
        0 2px 4px 0 #b2b2b2 inset,
        0 -1px 1px 0 #f2f2f2 inset,
        0 15px 15px 0 rgba(41, 41, 41, 0.09) inset;
}
button[type="submit"]{
    width:40px;
    height:30px;
    background-image:url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: 10px 5px;
    background-color:rgb(241, 234, 235);
    -webkit-background-size:20px 20px;
    background-size:20px 20px;
    border:none;
    cursor:pointer;
}
input[type="search"]:focus{
    outline:0;
} 




.result table {
	width:100%;
	margin-left:auto;
    margin-right:auto;
    margin-top:  100px;
	border:0;
}
.result th {
	background-color:#6FFFC4;
	/* color:#000000 */
}
.result,.result th,.result td {
	font-size:0.95em;
	text-align:center;
	padding:4px;
	border-collapse:collapse;
}
.result th,.result td {
	border: 1px solid #acfedd;
	border-width:1px 0 1px 0
}
.result tr {
	border: 1px solid #acfedd;
}
.result tr:nth-child(odd){
	background-color:#cffeeb;
}
.result tr:nth-child(even){
	background-color:#fdfdfd;
}
@media (max-width: 768px) {
    .result,.result th,.result td{
        font-size:12px;
    }
    .searchContainer img{
        width:50px;
        height:100px;
        border-radius: 1em;
    }

}